.feature {
  display: flex;
  flex-direction: column;
  gap: 5.625rem;
}
@media (min-width: 40em) {
  .feature {
    flex-direction: row;
    align-items: center;
    gap: 6.25rem;
  }
}
.feature__illus img {
  width: 100%;
}
@media (min-width: 40em) {
  .feature__illus--order {
    order: 2;
  }
}
@media (min-width: 40em) {
  .feature__illus {
    flex: 1;
  }
}
.feature__content h2 {
  font-size: 1.5625rem;
  font-family: var(--font-heading);
}
@media (min-width: 40em) {
  .feature__content h2 {
    font-size: 1.875rem;
  }
}
@media (min-width: 40em) {
  .feature__content p {
    font-size: 1rem;
  }
}
@media (min-width: 40em) {
  .feature__content--order {
    order: 1;
  }
}
@media (min-width: 40em) {
  .feature__content {
    flex: 1;
    text-align: left;
  }
}